// src/api/client.ts
import { getAccessToken, setAccessToken, clearAccessToken } from '../auth/tokenStore';

const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3000';

export type ApiError = {
  status: number;
  message: string;
};

async function fetchWithAuth(input: RequestInfo, init: RequestInit = {}, retry = true): Promise<any> {
  let token = getAccessToken();
  const headers = new Headers(init.headers || {});
  if (token) headers.set('Authorization', `Bearer ${token}`);
  let res = await fetch(`${API_BASE_URL}${input}`, { ...init, headers, credentials: 'include' });
  if (res.status === 401 && retry) {
    // Try refresh
    const refreshed = await refreshToken();
    if (refreshed) {
      token = getAccessToken();
      headers.set('Authorization', `Bearer ${token}`);
      res = await fetch(`${API_BASE_URL}${input}`, { ...init, headers, credentials: 'include' });
    } else {
      clearAccessToken();
      throw { status: 401, message: 'Session expired' };
    }
  }
  if (!res.ok) {
    let message = 'Unknown error';
    try { message = (await res.json()).message || message; } catch {}
    throw { status: res.status, message };
  }
  if (res.status === 204) return null;
  return res.json();
}

async function refreshToken(): Promise<boolean> {
  try {
    const res = await fetch(`${API_BASE_URL}/auth/refresh`, { method: 'POST', credentials: 'include' });
    if (!res.ok) return false;
    const data = await res.json();
    if (data.accessToken) {
      setAccessToken(data.accessToken);
      return true;
    }
    return false;
  } catch {
    return false;
  }
}

export const api = {
  get: (url: string) => fetchWithAuth(url, { method: 'GET' }),
  post: (url: string, body?: any) => fetchWithAuth(url, { method: 'POST', body: JSON.stringify(body), headers: { 'Content-Type': 'application/json' } }),
  patch: (url: string, body?: any) => fetchWithAuth(url, { method: 'PATCH', body: JSON.stringify(body), headers: { 'Content-Type': 'application/json' } }),
  delete: (url: string) => fetchWithAuth(url, { method: 'DELETE' }),
};

// Typed helpers (example)
export async function login(email: string, password: string) {
  return api.post('/auth/login', { email, password });
}

export async function getMe() {
  return api.get('/users/me');
}
